import React, { Component } from "react";
import { Grid, Row, Col, Button, Nav, Navbar, NavItem } from "react-bootstrap";
import FontAwesome from "react-fontawesome";
import YouTube from "react-youtube";
import objective from "./files/Objectives.pdf";
import logo from "./images/logo.jpg";
import "./App.css";

class App extends Component {
  openPdf = () => {
    window.open(objective);
  };

  openFacebook = () => {
    window.open("https://www.facebook.com/groups/303296586789628/");
  };

  _onReady = event => {
    // access to player in all event handlers via event.target
    event.target.pauseVideo();
  };

  render() {
    const opts = {
      height: "300",
      width: "500",
      playerVars: {
        // https://developers.google.com/youtube/player_parameters
        autoplay: 1
      }
    };
    return (
      <div className="App">
        <Navbar inverse collapseOnSelect>
          <Navbar.Header>
            <Navbar.Brand>
              <img src={logo} className="logo" />
            </Navbar.Brand>
            <Navbar.Toggle />
          </Navbar.Header>
          <Navbar.Collapse>
            <Nav pullRight>
              <NavItem onClick={this.openPdf} className="objective">
                Our Objectives
              </NavItem>
              <NavItem onClick={this.openFacebook}>
                <i className="fab fa-facebook-square fa-2x" />
              </NavItem>
            </Nav>
          </Navbar.Collapse>
        </Navbar>
        <Grid>
          <Row className="show-grid title-row">
            <Col xs={12} md={12}>
              <div className="title">
                Adoptee <span className="right">RIGHTS</span> Australia
              </div>
            </Col>
          </Row>
          <Row className="show-grid description-row">
            <Col xs={12} md={12}>
              <div className="description">
                National advocacy association for adopted people Adoptee Rights Australia (ARA) is the National Peak
                Body formed to give a voice to adoptees in Australia
                {"\n"}
                If you want to join and you support the following objectives please apply to be an associate member of
                this{" "}
                <a href="https://www.facebook.com/groups/303296586789628/" target="_blank">
                  Facebook page.
                </a>
              </div>
            </Col>
          </Row>
          <Row className="show-grid video-row">
            <Col xs={6} md={6}>
              <a href="https://vimeo.com/163495367" target="_blank">
                <div className="film">My Invisible Mother</div>
              </a>
              <p className="author">A short film by Pascal Huynh</p>
            </Col>
            <Col xs={6} md={6}>
              <YouTube videoId="a6pwFCludFs" opts={opts} onReady={this._onReady} />
            </Col>
          </Row>
          <Row className="show-grid resources-row">
            <Col xs={12} md={12}>
              <div className="resources">Resources</div>
              <a href="https://www.facebook.com/groups/364375293637544/" target="_blank">
                <div className="links">Australian Adoptee Rights Action Group</div>
              </a>
              <a href="https://www.facebook.com/AdoptionNewsSA/" target="_blank">
                <div className="links">Adoption News South Australia</div>
              </a>
              <a href="https://www.facebook.com/groups/133646350842978/about/" target="_blank">
                <div className="links">#adoptionredressnow</div>
              </a>
              <a href="https://www.facebook.com/AustralianStewardships/" target="_blank">
                <div className="links">Australian Stewardships Not Adoption Awareness Support Group</div>
              </a>
              <a href="https://www.facebook.com/groups/791292877610573/" target="_blank">
                <div className="links">Adoptees Speak</div>
              </a>
              <a href="https://www.facebook.com/ForcedAdoptionAustralia/" target="_blank">
                <div className="links">Forced Adoption-Australia</div>
              </a>
              <a href="https://intercountryadopteevoices.com/adoptee-voices/" target="_blank">
                <div className="links">InterCountry Adoptee Voices</div>
              </a>
            </Col>
          </Row>
        </Grid>
      </div>
    );
  }
}

export default App;



